import React from 'react'
import ResultsItem from './ResultsItem'
import Error from './Error'
import '../Components/ResultsList.css'

const ResultsList = ({ superheroes }) => {
  if (!superheroes) return <Error />
  // const resultsArray = superheroes.map((hero, i) => {
  //   return <ResultsItem className='hero--card' data={hero} id={hero.id} />
  // })
  const resultsArray = superheroes.map((hero, i) => {
    return <ResultsItem className='hero--card' data={hero} id={hero.id} />
  })
  return <div> {resultsArray} </div>
}

export default ResultsList
