import React from 'react';
import '../Components/ResultsItem.css';

const ResultsItem = (props) => {
  const { data } = props;

  const combat = data.powerstats.combat;
  const combatInformation = {
    height: '10px',
    width: `${combat}px`,
  };
  const intelligence = data.powerstats.intelligence;
  const intelligenceInformation = {
    height: '10px',
    width: `${intelligence}px`,
  };
  const speed = data.powerstats.speed;
  const speedInformation = {
    height: '10px',
    width: `${speed}px`,
  };
  const strength = data.powerstats.strength;
  const strengthInformation = {
    height: '10px',
    width: `${strength}px`,
  };
  const power = data.powerstats.power;
  const powerInformation = {
    height: '10px',
    width: `${power}px`,
  };
  const durability = data.powerstats.durability;
  const durabilityInformation = {
    height: '10px',
    width: `${durability}px`,
  };

  return (
    <div className='hero--container'>
      <h2>{data.name}</h2>
      <div className='image--container'>
        <img alt='' width='300px' height='auto' src={data.image.url}></img>
        {/* <p>{id}</p> */}
      </div>

      <div className='info--container'>
        <h3>{data.biography['full-name']}</h3>
        <p> {data.work.occupation}</p>
        <div className='card-powerstats'>
          <div className='powerstat'>
            <div className='powerstat--name'> Combat</div>
            <div className='powerstat--container'>
              <div className='black' style={combatInformation}></div>
            </div>
          </div>
          <div className='powerstat'>
            <div className='powerstat--name'> Intelligence</div>
            <div className='powerstat--container'>
              <div className='black' style={intelligenceInformation}></div>
            </div>
          </div>
          <div className='powerstat'>
            <div className='powerstat--name'> Strength</div>
            <div className='powerstat--container'>
              <div className='black' style={strengthInformation}></div>
            </div>
          </div>
          <div className='powerstat'>
            <div className='powerstat--name'> Speed</div>
            <div className='powerstat--container'>
              <div className='black' style={speedInformation}></div>
            </div>
          </div>
          <div className='powerstat'>
            <div className='powerstat--name'> Durability</div>
            <div className='powerstat--container'>
              <div className='black' style={durabilityInformation}></div>
            </div>
          </div>
          <div className='powerstat'>
            <div className='powerstat--name'> Power</div>
            <div className='powerstat--container'>
              <div className='black' style={powerInformation}></div>
            </div>
          </div>
        </div>
      </div>
      <div class='division'></div>
    </div>
  );
};

export default ResultsItem;
