import React, { useState } from 'react';
import './App.css';
import SearchBox from '../Components/Searchbox';
import ResultsList from '../Components/ResultsList';
import { accessAPI } from '../config/config';

//TUTORIAL https://www.youtube.com/watch?v=y5geukYjgBk

function App() {
  const [superheroes, setSuperheroes] = useState([]);
  const [searchField, setSearchField] = useState('');

  // const searchBox = 'superman';

  async function searchSuperHeroes() {
    fetch(
      `https://www.superheroapi.com/api.php/${accessAPI}/search/${searchField}`
    )
      .then((response) => response.json())
      .then((data) => {
        setSuperheroes(data.results);
      });
  }

  const handleChange = (e) => {
    const searchTerm = e.target.value;

    setSearchField(searchTerm);
    if (searchTerm.length <= 0) {
      setSuperheroes([]);
    }

    if (searchTerm.length > 2) {
      searchSuperHeroes();
    }
  };

  return (
    <div className='app'>
      <div className='hero'>
        <SearchBox searchField={searchField} handleChange={handleChange} />

        <svg
          className='clouds'
          width='100%'
          height='150'
          fill='none'
          xmlns='http://www.w3.org/2000/svg'
        >
          <path
            d='M1536.39 258H0V117.813c17.911-47.144 57.921-63.92 120.03-50.33 33.869-35.032 73.879-39.694 120.03-13.986 43.125-3.715 67.131 10.794 72.018 43.528 68.051-9.086 108.061 12.198 120.031 63.852 29.787-13.833 53.793-9.447 72.018 13.158 31.461-12.563 55.467-6.904 72.018 16.977 16.171-48.465 56.181-66.558 120.03-54.279-14.163-47.194 9.843-76.165 72.018-86.91 102.106 16.764 142.116 63.895 120.03 141.394-17.377-49.635 6.629-81.047 72.018-94.238 26.647-40.513 66.659-50.657 120.029-30.43 47.07-25.02 87.08-19.667 120.03 16.058 27.39-15.656 51.39-13.092 72.02 7.69 6.84-31.25 30.85-44.278 72.02-39.085 29.63-13.95 53.64-9.675 72.02 12.824 90.43 7.906 130.44 46.182 120.03 114.828V258Z'
            fill='#fff'
          />
          <path
            d='M1464 263.963H0V157.084c54.647 11.777 78.647 66.079 72 162.906-2.28-89.09 21.72-135.649 72-139.677C156.48 60.664 196.48 11.897 264 34.01c71.42-15.203 111.42 40.47 120 167.016 19.153-51.112 43.153-59.706 72-25.78 26.54-94.725 66.54-118.58 120-71.566 16.547-55.732 40.547-68.938 72-39.62 36.087-77.808 76.087-84.736 120-20.784 50.687-51.939 90.687-33.004 120 56.807 81.7 3.012 121.7 76.899 120 221.661-13.48-108.935 10.52-175.344 72-199.229 4.53-77.018 28.53-111.516 72-103.496 67.83 35.143 91.83 112.816 72 233.02 55.98-42.549 95.98-14.229 120 84.961 32.62-83.951 72.62-97.022 120-39.211v-33.826Z'
            fill='#fff'
          />
        </svg>
      </div>
      <div className='results'>
        <ResultsList superheroes={superheroes} />
      </div>
    </div>
  );
}

export default App;
